body {
  padding: 0px;
  margin: 0px;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-areas: 'header header' 'sidebyside1 sidebyside1img' 'title1 title1' 'card-container card-container' 'video video' 'title2 title2' 'sidebyside2 sidebyside2img' 'sidebyside3 sidebyside3img' 'sidebyside4 sidebyside4img' 'footer footer';
}

.section-1 nav {
  background-color: blue;
  display: grid;
  grid-template-columns: 50% 50%;
  padding-top: 30px;
  padding-left: 20px;
  padding-right: 20px;
}

.section-1 nav h3 {
  background-color: red;
}

.section-1 nav ul li {
  float: right;
  list-style-type: none;
}

.info-container {
  padding: 70px;
  text-align: center;
}

.info-container p {
  margin: 0px auto;
  width: 60%;
}

.left {
  text-align: left;
}

.left p {
  width: 80%;
  text-align: left;
  margin: 0px;
}

